.gridWrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Default columns */
  grid-template-rows: repeat(2, 1fr); /* Default rows */
  padding: 0; /* Default padding */
  gap: 10px; /* Default gap */
}

.name {
  background-color: white;
  width: 100%;
  height: 100dvh;
}
